.footer-newsletter-section {
  background-color: var(--beige);
  padding: 35px 0;
  border: 1px solid var(--lightGrey);
  .flex-container {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    max-width: 1100px !important;
    @media (max-width: 900px) {
      flex-direction: column;
    }
    .content {
      .title {
        font-size: var(--columnSize);
        font-weight: var(--regular);
        color: var(--darkGrey);
      }
      .subtitle {
        font-size: var(--paragraphSize);
        font-weight: var(--regular);
        color: var(--grey);
        margin-top: 10px;
      }
    }
    .form {
      .hs-form {
        display: flex;
        align-items: flex-end;
        flex-wrap: wrap;
      }
      @media (max-width: 900px) {
        margin-top: 10px;
      }
      label {
        font-size: var(--paragraphSize);
        color: var(--darkGrey);
      }
      .hs_email {
        font-size: var(--paragraphSize);
        color: var(--darkGrey);
      }
      .input {
        margin-top: 5px;
        display: flex;
        @media (max-width: 600px) {
          flex-direction: column;
        }
        .error {
          margin: 0 0 0 0;
          letter-spacing: 0;
          font-weight: 400;
        }
        input {
          padding: 5px;
          font-size: var(--paragraphSize) !important;
          color: var(--grey);
          border-radius: 0;
          border: 1px solid var(--darkGrey);
          height: 40px;
          width: 400px;
          @media (max-width: 600px) {
            width: 92vw;
            display: block;
            margin-bottom: 10px;
            max-width: 100%;
          }
          &::focus-visible {
          }
        }
        input:focus-visible {
          border-radius: 0 !important;
          outline: 1px solid var(--black) !important;
        }
      }
      .hs-button {
        width: 120px;
        height: 40px;
        line-height: 1.3em;
        padding: 10px 30px 10px 30px;
        cursor: pointer;
        font-weight: var(--regular);
        border-radius: 0 !important;
        border: 1px solid var(--black);
        background: var(--black);
        color: white !important;
        text-transform: uppercase;
        font-size: var(--paragraphSize);
        letter-spacing: 0.05rem;
        text-decoration: none;
        margin-left: 5px;
      }
      // error container

      .hs-error-msgs {
        position: absolute;
        list-style: none;
        li {
          label {
            color: var(--red);
          }
        }
      }
      .mc4wp-form-fields {
        margin-top: 5px;
        display: flex;
        @media (max-width: 600px) {
          flex-direction: column;
        }
        input {
          padding: 5px;
          font-size: var(--paragraphSize);
          color: var(--grey);
          border-radius: 0;
          border: 1px solid var(--darkGrey);
          margin-right: 5px;
          height: 40px;
          width: 400px;
          @media (max-width: 600px) {
            width: 92vw;
            display: block;
            margin-bottom: 10px;
            max-width: 100%;
          }
        }
        .primary-button {
          width: 120px;
          height: 40px;
        }
      }
    }
  }
  .privacy-policy {
    max-width: 1100px !important;
    font-size: 0.7rem;
    color: var(--grey);
    margin-top: 30px;
    @media (max-width: 400px) {
      margin-top: 10px;
    }
  }
}

footer {
  background: var(--beige);
  .flex-container {
    padding: 30px 0;
    @media (max-width: 600px) {
      padding: 20px 0 10px 0;
    }
    max-width: 1100px !important;
    .columns {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      .flex-item {
        margin: 0 10px 20px 10px;
        .title {
          font-size: var(--mediumSize);
          font-weight: var(--regular);
          color: var(--darkGrey);
        }
        .nav {
          margin-top: 15px;
          ul {
            list-style: none;
            li {
              font-size: var(--paragraphSize);
              color: var(--grey);
              margin: 10px 0;
              a {
                text-decoration: none;
                color: var(--grey);
                letter-spacing: 0.06rem;
                &:hover {
                  text-decoration: underline;
                }
              }
            }
          }
        }
        .social-icons {
          a {
            text-decoration: none;
            font-size: 1.5rem;
            margin: 0 10px 0 0;
            i {
              color: var(--grey);
              &:hover {
                color: var(--black);
              }
            }
          }
        }
        .contact-container {
          margin: 10px 0;
          .contact-links {
            .wd-phone-modal-container {
              display: block;
              position: static;
              .content-container {
                margin: 0;
                padding: 0;
                text-align: left;
                border: none;
                .content {
                  margin-bottom: 5px;
                  .phone-number {
                    color: var(--grey);
                    letter-spacing: 0.06rem;
                    text-decoration: none;
                  }
                  .title {
                    display: none;
                  }
                  a {
                    margin-bottom: 0;
                    color: var(--grey);
                    &:hover {
                      text-decoration: underline;
                    }
                  }
                }
              }
            }
            margin-top: 5px;
            a {
              margin-bottom: 10px;
              display: flex;
              align-items: center;
              text-decoration: none;
              svg {
                fill: #636363;
              }
              span {
                font-size: var(--mediumSize);
                margin-left: 5px;
                line-height: 0;
                color: var(--grey);
              }
            }
          }
        }
      }
    }
  }
  .copyright-section {
    padding: 25px 0;
    @media (max-width: 600px) {
      padding: 10px 0;
    }
    .columns {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;
      .links {
        margin-bottom: 5px;
        a {
          font-size: var(--paragraphSize);
          color: var(--grey);
          text-decoration: none;

          &:hover {
            text-decoration: underline;
          }
        }
        .first-child {
          &::after {
            color: #ccc;
            content: "|";
            display: inline-block;
            margin: 0 10px;
          }
        }
      }
    }
    .copyright {
      font-size: 0.7rem;
      color: var(--grey);
    }
  }
}

// arrow up
/* arrow to the header */
.go-to-header {
  position: fixed;
  top: 50%;
  right: 10px;
  transform: translate(0, -50%);
  z-index: 100;
}
.go-to-header a {
  padding: 10px 20px;
  background-color: rgba(0, 0, 0, 0.4);
}
.go-to-header a svg {
  // font-size: 40px;
  // background: rgba(0, 0, 0, 0.5);
  // padding: 0px 10px;
  // color: white;
}
.show {
  display: block !important;
}
.hide {
  display: none;
}

// fixed phone container
.fixed-phone-container {
  .call-btn {
    text-transform: capitalize;
    position: fixed;
    bottom: 100px;
    right: 10px;
    background-color: #b1c3b1;
    border-radius: 999rem !important;
    border: 1px solid #b1c3b1;
    padding: 12px 22px !important;
    letter-spacing: 0.6;
    font-weight: 700;
    font-size: 15px;
    width: 120px;
    i {
      font-size: 15px;
      color: #404b40;
    }
    span {
      color: #404b40;
    }
  }
}
.sticky-phone-button {
  position: fixed;
  bottom: 100px;
  right: 0px;
  z-index: 1000;
  .btn {
    width: 110px;
    height: 45px;

    padding: 15px 20px !important;
    border-radius: 999rem !important;
    bottom: 0;
    letter-spacing: 0.6;
    background-color: #b1c3b1 !important;
    border: none;
    @media (max-width: 1000px) {
      border-radius: 50% !important;
      width: 50px;
      height: 50px;
      padding: 0 !important;
    }
    .phone-icon {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 16px;
    }
    span {
      font-size: 0.95rem !important;
      font-weight: 700;
      color: #404b40 !important;
      margin-left: 5px;
      @media (max-width: 1000px) {
        display: none;
      }
    }
    .close-icon {
      display: none;
    }
  }
  .wd-phone-modal-container {
    background-color: var(--darkGrey);
    .content-container {
      .content {
        .title {
          color: var(--beige);
        }
        .phone-number {
          color: var(--beige);
          a {
            color: var(--beige);
          }
        }
      }
    }
  }
}
.wd-phone-modal-container {
  background-color: var(--beige);
  width: 245px;
  position: absolute;
  bottom: 50px;
  right: 20px;
  height: auto;
  text-align: center;
  display: none;
  z-index: 1000;
  .content-container {
    border: 1px solid var(--lightGrey);
    margin: 10px;
    padding: 10px;
    .content {
      margin-bottom: 10px;

      .title {
        font-size: var(--mediumSize);
        font-weight: var(--semiBold);
      }
      .phone-number {
        font-size: var(--paragraphSize);
        a {
          display: block;
        }
      }
    }
  }
}
